.detail {

  &__photos {
    @include pageGrid;
    padding-bottom: 16px;
  }
  &__link {
    position: relative;
    &--wide {
      @include onTablet {
        grid-column: span 4;
      }
      @include onDesktop {
        grid-column: span 8;
      }
    }
    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }
      @include onDesktop {
        grid-column: span 4;
      }
    }
  }
  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include hover(transform, scale(1.05));
  }
  &__title {
    font-size: 24px;
    font-weight: 700;
    line-height: 34px;
    padding-bottom: 8px;
  }
}
